<template>
  <div class="details">

    <van-pull-refresh v-model="refreshing"
                      @refresh="onRefresh">
      <van-list v-model="loading"
                :error.sync="error"
                error-text="请求失败，点击重新加载"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad">
        <div class="box">
          <div class="list"
               v-for="(item, index) in list"
               :key="index">
            <div class="icon">
              <img src="@/assets/gold.png"
                   alt="">
            </div>
            <div class="itemMain">
              <div class="top">
                <div class="numberName">
                  <div>NFT编号</div>
                </div>
                <div class="number">{{item.num}}</div>
              </div>
              <div class="time">{{item.time}}</div>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  data () {
    return {
      id: '',
      loading: false,
      finished: false,
      refreshing: false,
      error: false,
      list: [
        {
          num: 4527425474,
          time: '202203028 09:29:54',
          isComposition: true
        }
      ]
    }
  },
  mounted () {
    this.id = this.$route.query.id
  },
  methods: {
    onLoad () {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = []
          this.refreshing = false
        }
        for (let i = 0; i < 10; i++) {
          this.list.push(this.$options.data.call(this).list[0]);
        }
        this.loading = false
        if (this.list.length >= 40) {
          this.finished = true
        }
      }, 500)
    },
    onRefresh () {
      this.finished = false
      this.loading = true
      this.onLoad()
    },
  }
}
</script>
<style scoped lang="less">
.details {
  padding: 7px 10px;
  .box {
    background: #ffffff;
    border-radius: 10px;
    padding: 20px;
    padding-bottom: 0;
    box-sizing: border-box;
    .list:last-child {
      padding-bottom: 10px;
    }
    .list {
      display: flex;
      align-items: center;
      padding-bottom: 23px;
      .icon {
        width: 44px;
        height: 36px;
        margin-right: 11px;
      }
      .details {
        margin-left: auto;
        display: flex;
        align-items: center;
        .row {
          height: 13px;
          width: 7px;
          margin-left: 13px;
        }
        .text {
          font-size: 14px;
          font-weight: 400;
          color: #1f75d9;
        }
      }
      .itemMain {
        display: flex;
        flex-direction: column;
        .top {
          display: flex;
          align-items: center;
          .numberName {
            width: 43px;
            height: 15px;
            font-size: 9px;
            font-weight: 400;
            color: #909cba;
            box-sizing: border-box;
            border: 1px solid #909cba;
            border-radius: 3px;
            line-height: 1;
            padding: 3px 4px;
            margin-right: 10px;
            line-height: 1;
            position: relative;
            div {
              position: absolute;
              top: 1px;
              left: -3px;
              white-space: nowrap;
              font-size: 12px;
              transform: scale(0.75);
              color: #909cba;
            }
          }
          .number {
            font-size: 14px;
            font-weight: 400;
            color: #333333;
            line-height: 1;
          }
        }
        .time {
          margin-top: 9px;
          font-size: 10px;
          font-weight: 400;
          color: #909cba;
        }
      }
    }
  }
}
</style>